import styled from "styled-components";

const Wrapper = styled.div`
  height: 30vw;
  display: flex;
  align-items: center;
`;

const TeapotIcon = styled.div`
  width: 20vw;
  height: 100%;
  border-radius: 5px 5px 20px 20px;
  border: 4px solid black;
  display: flex;
  align-items: flex-end;
`;

const TeapotWater = styled.div`
  width: 100%;
  background: #70f1ff;
  border-radius: 1px 1px 15px 15px;
  height: ${({amount, max}) => (amount/max)*100}%;
  z-index: -1;
`;

const ScaleWrapper = styled.div`
  height: 115%;
  display: flex;
  flex-direction: column;
  align-items: end;
  justify-content: space-between;
`;

const ScaleText = styled.div`
  position: relative;
  left: -9px;
  font-weight: bold;
  white-space: pre-wrap;
`;

const Teapot = ({
  max,
  amount,
  onClick,
}) => {
  const onClickIcon = (e) => {
    console.log(e);
    const elementHeight = e.target.offsetHeight;
    const yPos = e.clientY - e.target.offsetTop;
    if (yPos <= 4) {
      onClick(max);
    } else if (yPos >= elementHeight - 4) {
      onClick(0);
    } else {
      const rate = 1 - (yPos / elementHeight);
      const value = Math.round((rate * max) / 10) * 10;
      onClick(value);
    }
  }

  return (
    <Wrapper>
      <TeapotIcon onClick={onClickIcon}>
        <TeapotWater max={parseInt(max)} amount={parseInt(amount)} />
      </TeapotIcon>
      <ScaleWrapper>
          <ScaleText> {max||100}</ScaleText>
          <ScaleText>- {(max||100)/4*3}</ScaleText>
          <ScaleText>- {(max||100)/2}</ScaleText>
          <ScaleText>- {(max||100)/4*1}</ScaleText>
          <ScaleText> 0</ScaleText>
        </ScaleWrapper>
    </Wrapper>
  );
};

export default Teapot;